<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1></h1>
            <div class="next"></div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  
                </p>
                <div class="h-center">
                <el-row>
                  <el-col :span="12">
                    <span class="h-center">
                      <p></p>
                      <img :src="img1" alt=""
                    /></span>
                  </el-col>
                  <el-col :span="12">
                    <span class="h-center"
                      ><p></p>
                      <img :src="img2" alt=""
                    /></span>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <span class="h-center">
                      <p></p>
                      <img :src="img3" alt=""
                    /></span>
                  </el-col>
                  <el-col :span="12">
                    <span class="h-center"
                      ><p></p>
                      <img :src="img4" alt="" />
                    </span>
                  </el-col>
                </el-row>
                </div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center"> </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  name: "leftPanel",
  data: function () {
    return {
      show: false,
      showContent: true,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      img1: require("../img/.png"),
      img2: require("../img/.png"),
      img3: require("../img/.png"),
      img4: require("../img/.png"),
    };
  },
  // props:["showPC"],

  methods: {
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      // this.initChart1();
      // this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // this.chart1.dispose();
    this.chart1 = null;
    this.chart2 = null;
  },
};
</script>


<style lang="less" scoped>

.pmain {
  height: 650px;
}


.h-center {
  text-align: center;
}

.h-center img {
  height: 150px;
  width: calc(100%-20px);
}
.bl h1 {
  color: #fff;
}
.bl h2 {
  color: #fff;
}
.bl p {
  color: #fff;
}
.bl b {
  color: #1dbace;
}

#chart1 {
  width: calc(100%-20px);
  height: 200px;
}
#chart2 {
  width: calc(100%-20px);
  height: 200px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}

.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.2s;
}
</style>