<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1></h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  <b></b
                  >2019787024.6%
                </p>

                <div id="chart531"></div>

                <div id="chart532"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      huowufenlei: [
        {
          : "",
          : 952,
          : 0.120939,
        },
        {
          : "",
          : "1,067",
          : 0.135615,
        },
        {
          : "",
          : 105,
          : 0.013374,
        },
        {
          : "",
          : 20,
          : 0.002505,
        },
        {
          : "",
          : 174,
          : 0.022093,
        },
        {
          : "",
          : 53,
          : 0.006723,
        },
        {
          : "",
          : 260,
          : 0.033036,
        },
        {
          : "",
          : "3,246",
          : 0.412392,
        },
        {
          : "",
          : 107,
          : 0.01359,
        },
        {
          : "",
          : 1,
          : 0.000181,
        },
        {
          : "",
          : 320,
          : 0.040616,
        },
        {
          : "",
          : 46,
          : 0.005835,
        },
        {
          : "",
          : 1,
          : 0.000101,
        },
        {
          : "",
          : 2,
          : 0.000315,
        },
        {
          : "",
          : "1,516",
          : 0.192685,
        },
      ],
      gangquzhanbi: [
        {
          : "",
          : "",
          : 174,
          : 0.018182,
        },
        {
          : "",
          : "",
          : 377,
          : 0.039394,
        },
        {
          : "",
          : "",
          : 182,
          : 0.019018,
        },
        {
          : "",
          : "",
          : 169,
          : 0.017659,
        },
        {
          : "",
          : "",
          : "6,734",
          : 0.703657,
        },
        {
          : "",
          : "",
          : 138,
          : 0.01442,
        },
        {
          : "",
          : "",
          : 938,
          : 0.098015,
        },
        {
          : "",
          : "",
          : 418,
          : 0.043678,
        },
        {
          : "",
          : "",
          : 300,
          : 0.031348,
        },
        {
          : "",
          : "",
          : 80,
          : 0.008359,
        },
        {
          : "",
          : "",
          : 60,
          : 0.00627,
        },
      ],
    };
  },
  props: ["showPC"],
  watch: {
    showPC(val) {
      this.showContent = val;
    },
  },
  methods: {
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart531"));
      var table = this.huowufenlei;
      var xdata = [];
      var ydata = [];
      for (var i = 0; i < table.length; i++) {
        xdata.push(table[i].);
        ydata.push(table[i].);
      }
      this.chart1.setOption({
        tooltip: {
          //
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " "
            );
          },
        },
        legend: {
          icon: "rect",
          itemWidth: 15,
          itemHeight: 10,
          itemGap: 20,
          top: "0px",
          textStyle: {
            fontSize: 12,
            color: "#fff",
          },
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["30%", "50%"],
            center: ["50%", "60%"],
            avoidLabelOverlap: true,
            label: {
              normal: {
                show: true,
                textStyle: {
                  fontSize: "15",
                  color: "#fff",
                },
              },
            },
            data: (function (xdata, ydata) {
              var arr = [];
              for (var i = 0; i < xdata.length; i++) {
                arr.push({
                  name: xdata[i],
                  value: ydata[i],
                });
              }
              return arr;
            })(xdata, ydata),
          },
        ],
      });
    },
    initChart2() {
      var table = this.gangquzhanbi;
      // 
      table.sort(function (a, b) {
        return b. - a.;
      });
      var xdata = [];
      var ydata = [];
      for (var i = 0; i < table.length; i++) {
        ydata.push(table[i].);
        xdata.push(table[i].);
      }
      this.chart2 = echarts.init(document.getElementById("chart532"));
      this.chart2.setOption({
        tooltip: {
          show: true,
          trigger: "item",
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              (params.value * 100).toFixed(1) +
              " %"
            );
          },
        },
        grid: {
          x: 75,
          y: 35,
          x2: 65,
          y2: 25,
        },
        legend: {
          show: true,
          textStyle: {
            color: "#fff",
          },
        },
        yAxis: [
          {
            type: "category",
            inverse: true,
            data: ydata,
            axisLine: {
              show: true,
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
          },
        ],
        xAxis: [
          {
            type: "value",
            axisLine: {
              show: true,
              lineStyle: {
                color: "#fff",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "",
            type: "bar",
            barWidth: "80%",
            itemStyle: {
              normal: {
                color: "#5CF4F8",
              },
            },
            data: xdata,
            label: {
              show: true,
              position: "right",
              color: "#fff",
              formatter: function (params) {
                return (params.value * 100).toFixed(1) + "%";
              },
            },
          },
        ],
      });
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-4");
        this.$emit("subTitle", "5-3");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-2");
        this.$emit("subTitle", "5-1");
      }, 50);
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    // console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>


.pmain {
  height: 650px;
}

.bl h1 {
  color: #fff;
}
.bl p {
  color: #fff;
}

#chart531 {
  width: calc(100%-20px);
  height: 350px;
}
#chart532 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
</style>