<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1></h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <p>
                  <b class="green"></b
                  ><b
                    class="green"
                    ></b
                  > 
                </p>
                <div id="chart231"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      traffic_rank: [
        ["", "", 0.9],
        ["", "", 0.83],
        ["", "", 0.82],
        ["", "", 0.81],
        ["", "", 0.8],
        ["", "", 0.79],
        ["", "", 0.77],
        ["", "", 0.75],
      ],
      toll_rank: [
        ["", "", 79729],
        ["", "", 54861],
        ["", "", 48447],
        ["", "", 43986],
        ["", "", 42246],
        ["", "", 40907],
        ["", "", 40383],
        ["", "1", 39385],
        ["", "", 39022],
        ["", "", 34989],
      ],
    };
  },
  props: ["showPC"],

  methods: {
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-3");
        this.$emit("subTitle", '5-3');
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "5-5");
        this.$emit("subTitle", '5-5');
      }, 50);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
    initChart1() {
      this.chart1 = echarts.init(document.getElementById("chart231"));
      var tabledata = [
        {
          To: "",
          From: "",
          value: 89071,
          : "23.9%",
        },
        {
          To: "",
          From: "",
          value: 57033,
          : "15.3%",
        },
        {
          To: "",
          From: "",
          value: 40143,
          : "10.8%",
        },
        {
          To: "",
          From: "",
          value: 38784,
          : "10.4%",
        },
        {
          To: "",
          From: "",
          value: 35712,
          : "9.6%",
        },
        {
          To: "",
          From: "",
          value: 35332,
          : "9.5%",
        },
        {
          To: "",
          From: "",
          value: 30846,
          : "8.3%",
        },
        {
          To: "",
          From: "",
          value: 21563,
          : "5.8%",
        },
        {
          To: "",
          From: "",
          value: 21260,
          : "5.7%",
        },
        {
          To: "",
          From: "",
          value: 2849,
          : "0.8%",
        },
      ];
      var toScatterData = function (data) {
        var scatterData = [];
        data.forEach((item, index) => {
          // console.log([item.value, index, item])
          scatterData.push([item.value, index, item]);
        });
        return scatterData;
      };
      var scatterData = toScatterData(tabledata);
      this.chart1.setOption({
        tooltip: {
          //
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          formatter: function (params) {
            return (
              "<b>" +
              params.data.From +
              " - " +
              params.data.To +
              "</b></br>" +
              params.marker +
              "&nbsp;" +
              params.data.value +
              "&nbsp; Pcu/d</br>" +
              params.marker +
              "&nbsp;" +
              params.data. 
            );
          },
        },
        grid: {
          x: 90,
          y: 25,
          x2: 20,
          y2: 45,
        },
        xAxis: [
          {
            type: "value",
            name: "(Pcu/d)",
            nameLocation: "middle",
            nameTextStyle: {
              color: "azure",
              padding: [10, 0, 0, 0],
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //
              showMaxLabel: true,
              interval: 0,
            },
            max: 100000,
            axisTick: {
              show: false,
              lineStyle: {
                color: "grey",
              },
            },
            splitLine: {
              lineStyle: {
                color: "grey",
              },
            },
          },
        ],
        yAxis: [
          {
            nameLocation: "start",
            name: "",
            nameGap: 5,
            type: "category",
            data: [
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              "",
            ],
            inverse: true,
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 10,
              },
              //
              showMaxLabel: true,
              interval: 0,
            },
            nameTextStyle: {
              color: "azure",
              padding: [0, 0, 0, -60],
            },
          },
        ],
        series: [
          {
            type: "bar",
            name: "",
            itemStyle: {
              color: "#24FFC5",
              opacity: 0.8,
            },
            barWidth: 2.5,
            label: {
              show: true,
              position: "right",
              textStyle: {
                color: "azure",
                padding: [0, 0, 0, 10],
              },
            },

            data: tabledata,
          },
          {
            type: "scatter",
            symbolSize: function (val) {
              return val[0] / 3000;
            },
            tooltip: {
              formatter: function (params) {
                return (
                  "<b>" +
                  params.data[2].From +
                  " - " +
                  params.data[2].To +
                  "</b></br>" +
                  params.marker +
                  "&nbsp;" +
                  params.data[2].value +
                  "&nbsp; Pcu/d</br>" +
                  params.marker +
                  "&nbsp;" +
                  params.data[2]. 
                );
              },
            },
            itemStyle: {
              color: "#24FFC5",
              opacity: 1,
            },
            data: scatterData,
          },
        ],
      });
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      this.initChart1();
      // this.initChart2();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    console.log("beforeUnmount");
    if (!this.chart1 && !this.chart2) {
      return;
    }
    // window.removeEventListener('resize', this.__resizeHandler)
    this.chart1.dispose();
    this.chart1 = null;
    // this.chart2.dispose();
    this.chart2 = null;
  },
};
</script>


<style scoped>





#chart231 {
  width: calc(100%-20px);
  height: 250px;
}
#chart12 {
  width: calc(100%-20px);
  height: 300px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}
.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.3s;
}
.pmain {
  max-height: 650px;
}
</style>