<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1></h1>
            <div class="next">
              <el-button @click="previousPage"> &lt; </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <el-scrollbar class="pmain">
                <div id="defaultDisplay" v-show="!sectionClicked">
                  <!-- <p>202220212021<b>30342pcu/d</b>202010.3%201911.6%
                  </p> -->
                  <p>20231<b>32304 Pcu/d</b>
                  </p>
                  <p></p>
                  <p><b></b><b>40175 Pcu/d</b></p>
                  <p>
                    <b class="green"></b><b
                      class="green"
                      >27775 Pcu/d</b
                    >
                  </p>
                  <p>
                    <b class="red"></b
                    ><b class="red">18676 Pcu/d</b>
                  </p>
                  <div id="chart2"></div>
                  <p>
                    <b class="red"
                      >19327 Pcu/d</b
                    ><b class="lightblue">41181 Pcu/d</b>
                  </p>
                  <div id="chart3"></div>
                </div>
                <p id="descriptionSection"></p>
                <div id="chart4"></div>
              </el-scrollbar>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";
import { markRaw } from "vue";
export default {
  name: "leftPanel",
  data: function () {
    return {
      show: false,
      showContent: true,
      chart1: null,
      sectionClicked: false,
      sectionID: null,
      volume: [
        8467, 14437, 19384, 18878, 21514, 27596, 29276, 30812, 37693, 37831,
      ],
      chart2: null,
      chart3: null,
      chart4: null,
    };
  },
  props: ["showPC", "isSectionClicked", "clickedSectionID"],
  watch: {
    isSectionClicked(val) {
      this.sectionClicked = val;
    },
    clickedSectionID(val) {
      this.sectionID = val;
      console.log(val);
      this.initChart4();
    },
  },

  methods: {
    initChart2() {
      this.chart2 = echarts.init(document.getElementById("chart2"));
      this.chart2.setOption({
        tooltip: {
          //
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          confine: true,
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " Pcu/d</b>"
            );
          },
        },
        legend: {
          show: false,
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        grid: {
          x: 100,
          y: 25,
          x2: 25,
          y2: 25,
        },
        yAxis: [
          {
            type: "category",
            boundaryGap: true,
            axisLine: {
              lineStyle: {
                color: "#5CF4F8",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 15,
              },
              //
              showMaxLabel: true,
              interval: 0,
            },
            data: ["", "", "", ""],
          },
        ],
        xAxis: [
          {
            type: "value",
            axisTick: {
              show: true,
            },
            max: 50000,
            axisLine: {
              lineStyle: {
                color: "#5CF4F8",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 15,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "",
            type: "bar",
            smooth: true,
            barWidth: "15",
            label: {
              normal: {
                show: true,
                position: "right",
                formatter: "{c} Pcu/d",
                textStyle: {
                  color: "#fff",
                  fontSize: 15,
                },
              },
            },

            data: [
              {
                value: 32304,
                itemStyle: {
                  color: "gray",
                },
              },
              {
                value: 18676,
                itemStyle: {
                  color: "#F97952",
                },
              },
              {
                value: 27775,
                itemStyle: {
                  color: "#24FFC5",
                },
              },
              {
                value: 40175,
                itemStyle: {
                  color: "#5CF4F8",
                },
              },
            ],
          },
        ],
      });
    },
    initChart3() {
      this.chart3 = echarts.init(document.getElementById("chart3"));
      this.chart3.setOption({
        tooltip: {
          //
          trigger: "item",
          axisPointer: {
            lineStyle: {
              color: "#fff",
            },
          },
          confine: true,
          formatter: function (params) {
            return (
              params.seriesName +
              "</br>" +
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b>" +
              params.value +
              " Pcu/d</b>"
            );
          },
        },
        legend: {
          show: false,
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          right: "10px",
          top: "0px",
          textStyle: {
            fontSize: 10,
            color: "#fff",
          },
        },
        grid: {
          x: 100,
          y: 25,
          x2: 50,
          y2: 25,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            axisLine: {
              lineStyle: {
                color: "#5CF4F8",
              },
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 15,
              },
              //
              showMaxLabel: true,
              interval: 0,
            },
            data: ["", ""],
          },
        ],
        yAxis: [
          {
            type: "value",
            axisTick: {
              show: true,
            },
            max: 50000,
            axisLine: {
              lineStyle: {
                color: "#5CF4F8",
              },
              show: true,
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 15,
                color: "#fff",
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [
          {
            name: "",
            type: "bar",
            smooth: true,
            barWidth: "15",
            label: {
              normal: {
                show: true,
                position: "top",
                formatter: "{c} Pcu/d",
                textStyle: {
                  color: "#fff",
                  fontSize: 15,
                },
              },
            },
            markLine: {
              lineStyle: {
                color: "grey",
              },
              label: {
                position: "end",
                formatter: "",
                textStyle: {
                  color: "grey",
                },
              },
              symbol: ["circle", "arrow"],
              data: [{ yAxis: 32304, name: "" }],
            },

            data: [
              {
                value: 41181,
                itemStyle: {
                  color: "#5CF4F8",
                },
              },
              {
                value: 19327,
                itemStyle: {
                  color: "#F97952",
                },
              },
            ],
          },
        ],
      });
    },
    initChart4() {
      document.getElementById("chart4").style.height = "500px";
      var descriptionObj = document.getElementById("descriptionSection");


      this.chart4 = markRaw(echarts.init(document.getElementById("chart4")));
      var roaddata = require("../assets/data/road20220822_new.json");
      var features = roaddata.features;
      for (var i = 0; i < features.length; i++) {
        if (features[i].properties[""] == this.sectionID) {
          var feature = features[i];
        }
      }
      var hwName = feature.properties.;
      var o1 = feature.properties.1;
      var o2 = feature.properties.2;
      descriptionObj.innerHTML = "<b>"+hwName+"</b><b>"+o1+"</b><b>"+o2+"</b>"

      var huo1 = feature.properties.1;
      var huo2 = feature.properties.2;
      var huo3 = feature.properties.3;
      var huo4 = feature.properties.4;
      var huo5 = feature.properties.5;
      var huo6 = feature.properties.6;
      var ke1 = feature.properties.1;
      var ke2 = feature.properties.2;
      var ke3 = feature.properties.3;
      var ke4 = feature.properties.4;

      var sum_all = huo1+huo2+huo3+huo4+huo5+huo6+ke1+ke2+ke3+ke4;

      ke1 = ke1/sum_all;
      ke2 = ke2/sum_all;
      ke3 = ke3/sum_all;
      ke4 = ke4/sum_all;

      huo1 = huo1/sum_all;
      huo2 = huo2/sum_all;
      huo3 = huo3/sum_all;
      huo4 = huo4/sum_all;
      huo5 = huo5/sum_all;
      huo6 = huo6/sum_all;
      

      var datapie = [
        { value: huo1, name: "1" },
        { value: huo2, name: "2" },
        { value: huo3, name: "3" },
        { value: huo4, name: "4" },
        { value: huo5, name: "5" },
        { value: huo6, name: "6" },
        { value: ke1, name: "1" },
        { value: ke2, name: "2" },
        { value: ke3, name: "3" },
        { value: ke4, name: "4" },
      ];

      console.log(datapie);
      //
      this.chart4.setOption({
        tooltip: {
          trigger: "item",
          formatter:function(params){
            return (
              params.marker +
              " " +
              params.name +
              "&nbsp;&nbsp;&nbsp;&nbsp;<b class='black'>" +
              (params.value*100).toFixed(1)+
              " %</b>"
            );

          }
        },
        legend: {
          top: "5%",
          left: "center",
          itemWidth: 30,
          itemGap: 30,
          textStyle: {
            color: "#fff",
            fontSize: 15,
          },
        },
        label: {
          show: true,
          fontSize: 15,
          position: "outside",
          color: "#fff",
          formatter: function(params){
            return (
              params.name +
              "" +
              (params.value*100).toFixed(1) +
              " %"
            );
          },
        },
        labelLine: {
          show: true,
        },
        series: [
          {
            type: "pie",
            radius: ["10%", "40%"],
            center: ["50%", "60%"],
            avoidLabelOverlap: true,
            itemStyle: {
              borderRadius: 20,
              borderColor: "#ffffff00",
              borderWidth: 5,
            },
            color: [
              "#F97952",
              "#F97952F0",
              "#F97952E0",
              "#F97952D0",
              "#F97952C0",
              "#F97952B0",
              "#24FFC5",
              "#24FFC5F0",
              "#24FFC5E0",
              "#24FFC5D0",
            ],
            emphasis: {
              label: {
                show: true,
                fontSize: "16",
                fontWeight: "bold",
              },
            },

            data: datapie,
          },
        ],
      });
    },
    nextPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "channel");
        this.$emit("subTitle", "channel");
      }, 50);
    },
    previousPage() {
      this.show = false;
      setTimeout(() => {
        this.$router.push(this.$route.path + "?tn=" + "2-1");
        this.$emit("subTitle", "2-1");
      }, 50);
    },
    showContentFunction() {
      this.showContent = true;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", true);
      panelbox.style.height = "730px";
    },
    hideContentFunction() {
      this.showContent = false;
      var panelbox = document.getElementById("box");
      this.$emit("showContent", false);
      panelbox.style.height = "90px";
    },
  },
  mounted() {
    this.show = true;
    setTimeout(() => {
      // this.initChart1();
      this.initChart2();
      this.initChart3();
      this.showContent = this.showPC;
    }, 0);
  },
  beforeUnmount() {
    this.sectionClicked = false;
    this.sectionID = null;
    if (!this.chart1 && !this.chart2 && !this.chart3 && !this.chart4) {
      return;
    }

    // this.chart1.dispose();
    this.chart1 = null;
    this.chart2.dispose();
    this.chart2 = null;
    this.chart3.dispose();
    this.chart3 = null;
    if (this.chart4) {
      this.chart4.dispose();
      this.chart4 = null;
    }
  },
};
</script>


<style lang="less" scoped>
.pmain {
  height: 625px;
}

#chart1 {
  width: calc(100%-20px);
  height: 200px;
}
#chart2 {
  width: calc(100%-20px);
  height: 200px;
}
#chart3 {
  width: calc(100%-20px);
  height: 200px;
}
#chart4 {
  width: calc(100%-20px);
  height: 50px;
}

div.next {
  position: absolute;
  top: 30px;
  right: 20px;
}

.next .el-button {
  padding: 0 1 0 1;
  background: none;
  border: none;
  color: #fff;
}

.fade-enter-from {
  /* transform: translateX(50px); */
  opacity: 0;
}
.fade-leave-to {
  /* transform: translateX(50px); */
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.5s;
}
.fade-leave-active {
  transition: all 0.2s;
}

</style>