<template>
  <div class="float">
    <transition name="fade">
      <div v-if="show">
        <el-container>
          <el-header class="bl">
            <h1></h1>
            <div class="next">
              <el-button @click="previousPage">
                &lt;
              </el-button>
              <el-button @click="nextPage"> > </el-button>
            </div>
          </el-header>
          <el-main class="bl">
            <div v-show="showContent">
              <div class="pmain">
                <el-scrollbar style="height: 100%">
                  <p>
                    
