                  <p>
                    <b></b><b></b>429.261868.1/
                  </p>
                  <el-table :data="tableData" :stripe="true" border style="width: 100%">
                    <el-table-column prop="" label="" width="180" />
                    <el-table-column prop="" label=""  />
                  </el-table>
                </el-scrollbar>
              </div>
            </div>
          </el-main>
          <el-footer style="height: 20px; text-align: center">
            <div v-show="showContent" class="centerImg">
              <img
                @click="hideContentFunction"
                src="../img/up.png"
                alt=""
                style="height: 20px"
              />
            </div>
            <div v-show="!showContent" class="centerImg">
              <img
                @click="showContentFunction"
                src="../img/down.png"
                alt=""
                style="height: 20px"
              />
            </div>
          </el-footer>
        </el-container>
      </div>
    </transition>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "leftPanel",
  data: function () {
    return {
      showContent: true,
      show: false,
      chart1: null,
      chart2: null,
      tableData: [
        {
          : "",
          : "",
        },
        {
          : "",
          : "B*****",
        },
        {
          : "",
          : "",
        },
        {
          : "",
          : "2021/9/15  12:10:56",
        },
        {
          : "",
